.user{
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-areas: 'name logo' 'rating logo' 'followers logo' 'open-btn open-btn';
    gap: 1rem;
    width: min(45rem, 100%);
    padding: 1.5rem;
    margin: 0 auto;
    background-color: #f4f5f6;
    border-radius: .8rem;
}

.userName{
    grid-area: name;
    font-size: 2rem;
    font-weight: 600;
    overflow: hidden;
    text-overflow: ellipsis;
}

.userLogo{
    grid-area: logo;
    width: 10rem;
    height: 10rem;
    border-radius: .8rem;
    overflow: hidden;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.userRating{
    grid-area: rating;
    font-size: 1.4rem;
}

.userRole{
    grid-area: followers;
    font-size: 1.4rem;
}

.userOpenBtn{
    grid-area: open-btn;
}

.userOpenBtnLayout{
    width: 100%;
}