.main{
    display: grid;
    width: min(144rem, 100%);
    margin: 0 auto;
    padding: 2rem;
    grid-template-columns: auto 1fr auto;
    grid-template-areas: 'backBtn . .' 'logo login nav' ' logo raiting .' 'logo reps .' 'logo . .'  'logo . .' 'repsList repsList repsList';
    column-gap: 3rem;
    .userAvatar{
        grid-area: logo;
        width: 10rem;
        height: 10rem;
        border-radius: 100%;
        -webkit-border-radius: 100%;
        -moz-border-radius: 100%;
        -ms-border-radius: 100%;
        -o-border-radius: 100%;
        object-fit: cover;
        @media screen and (min-width:568px) {
            width: 20rem;
            height: 20rem;
        }
    }
}

.userLogin{
    grid-area: login;
    font-size: 2rem;
    font-weight: 600;
}

.userFollowers{
    grid-area: followers;
    font-size: 1.4rem;
}

.userFollowings{
    grid-area: followings;
    font-size: 1.4rem;
}

.userRaiting{
    grid-area: raiting;
    font-size: 1.4rem;
}

.userReps{
    grid-area: reps;
    font-size: 1.4rem;
    
}

.userNav{
    grid-area: nav;
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    margin-right: auto;
    @media screen and (min-width:992px) {
        gap: 3rem;
        grid-template-columns: 20.5rem 20.5rem;
    }
}

.backBtn{
    grid-area: backBtn;
    margin-bottom: 4rem;
    width: 3rem;
    height: 3rem;
    cursor: pointer;
    img{
        width: 100%;
        height: 3rem;
        object-fit: contain;
    }
}

.userRepsList{
    grid-area: repsList;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
    gap: 1.5rem;
    margin-top: 2rem;
}